body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background: #2c3e50;
    max-width: 900px;
    margin: auto;
}

button {
    border: none;
}

.status {
    margin-bottom: 10px;
}


.game {
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr;
}

.board-area {
    grid-area: 1 / 1 / 2 / 2;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    height: 50vh;
    justify-items: center;
    -ms-flex-align: center;
        align-items: center;
}

.game-board {
    grid-area: 1 / 1 / 3 / 3;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    height: 25rem;
    width: 25rem;
    -webkit-box-shadow: 2px 2px 8px rgba(0,0,0,0.3);
            box-shadow: 2px 2px 8px rgba(0,0,0,0.3);
    border-radius: 5px;
    background: -webkit-linear-gradient(315deg, #02ccba 0%, #feb941 40%, #c68326 85%);
    background: -o-linear-gradient(315deg, #02ccba 0%, #feb941 40%, #c68326 85%);
    background: linear-gradient(135deg, #02ccba 0%, #feb941 40%, #c68326 85%); /* #c68326 */
}

.board-row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
}

.square {
    border-radius: 5px;
    margin: 1.25rem;
    background: #3F91C7;
    font-size: 2rem;
    color: #e4e4e4;
    -webkit-box-shadow: 2px 2px 8px rgba(0,0,0,0.3);
            box-shadow: 2px 2px 8px rgba(0,0,0,0.3);
}

.square:hover {
    background: #2578AE;
}

.highlighted {
    color: #35eb7e;
}

.game-info {
    grid-area: 1 / 2 / 2 / 3;
    height: 25rem;
    width: 12.5rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: center;
        align-self: center;
    background: -webkit-linear-gradient(315deg, #02ccba 0%, #feb941 40%, #c68326 85%);
    background: -o-linear-gradient(315deg, #02ccba 0%, #feb941 40%, #c68326 85%);
    background: linear-gradient(135deg, #02ccba 0%, #feb941 40%, #c68326 85%);
    border-radius: 5px;
    -webkit-box-shadow: 2px 2px 8px rgba(0,0,0,0.3);
            box-shadow: 2px 2px 8px rgba(0,0,0,0.3);
    /* overflow-y: scroll; */
}

.game-info-sort {
    margin: 1rem;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    text-align: center;
}

.game-info-moves, .game-info-sort, .game-info-status {
    background: rgba(63, 145, 199, 0.4);
    -webkit-box-shadow: 1px 1px 8px rgba(0,0,0,0.3);
            box-shadow: 1px 1px 8px rgba(0,0,0,0.3);
    padding: .25rem .5rem .25rem .5rem;
    color: #ededed;
    border-radius: 10px;
}

.game-info-status {
    margin-top: 1rem;
    background: rgba(198, 131, 38, 0.6);
    color: #111;
}

.moves-enter {
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transform: scale(0);
            -ms-transform: scale(0);
        transform: scale(0);
}
.moves-enter.moves-enter-active {
    -webkit-transform: scale(1);
            -ms-transform: scale(1);
        transform: scale(1);
}

.moves-leave {
    -webkit-transition: all 0.15s;
    -o-transition: all 0.15s;
    transition: all 0.15s;
    -webkit-transform: scale(1);
            -ms-transform: scale(1);
        transform: scale(1);
}
.moves-leave.moves-leave-active {
    -webkit-transform: scale(0);
            -ms-transform: scale(0);
        transform: scale(0);
}

.game-info-moves:hover, .game-info-sort:hover {
    background: #2578AE
}


